.modal-backdrop {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 350px;
  width: 500px;
  padding: 14px;
  transform: translate(-50%, -50%);
  background-color: #fff;
}
